html.docs body {
  padding-left: 320px; }

html.docs .docs-sidebar {
  border-right: 1px solid #f0f4f7;
  background: #fafcfc;
  top: 42px;
  position: fixed;
  overflow: auto;
  width: 220px;
  bottom: 0;
  left: 0; }
  html.docs .docs-sidebar li a {
    padding: 6px 20px;
    display: block;
    color: #141823; }
    html.docs .docs-sidebar li a:hover {
      color: #207de5; }
  html.docs .docs-sidebar li:first-child a {
    padding-top: 15px; }
  html.docs .docs-sidebar li.current a {
    color: #207de5;
    font-weight: bold; }

html.docs .docs-content {
  position: fixed;
  overflow: auto;
  left: 220px;
  bottom: 0;
  top: 42px;
  right: 0; }
  html.docs .docs-content .docs-page {
    justify-content: space-around;
    flex-flow: row wrap;
    min-height: 100%;
    display: flex; }
    html.docs .docs-content .docs-page::before {
      display: table;
      content: ''; }
    html.docs .docs-content .docs-page::after {
      display: table;
      clear: both;
      content: ''; }
    html.docs .docs-content .docs-page-toggle {
      float: right;
      margin-right: 20px;
      line-height: 50px; }
      html.docs .docs-content .docs-page-toggle button {
        transition: transform 0.175s cubic-bezier(0.3, 0, 0, 1.3);
        display: inline-block;
        margin-right: 6px;
        user-select: none;
        background: 0;
        border: 0; }
        html.docs .docs-content .docs-page-toggle button:focus, html.docs .docs-content .docs-page-toggle button:active {
          border: 0;
          outline: 0;
          box-shadow: none; }
        html.docs .docs-content .docs-page-toggle button:hover {
          transform: scale(1.1); }
    html.docs .docs-content .docs-page-header {
      top: 42px;
      background: #242729;
      visibility: hidden;
      position: fixed;
      color: #ffffff;
      z-index: 50;
      left: 220px;
      right: 0; }
      html.docs .docs-content .docs-page-header-title {
        margin: 0;
        font-size: 20px;
        padding: 10px;
        line-height: 30px; }
    html.docs .docs-content .docs-page-content {
      border-right: 1px solid #f0f4f7;
      background-color: #ffffff;
      visibility: hidden;
      min-width: 500px;
      flex-basis: 50%;
      flex-grow: 1;
      padding: 10px;
      padding-top: 50px; }
      html.docs .docs-content .docs-page-content-block {
        padding: 0 10px 20px;
        border-bottom: 1px solid #f0f4f7;
        margin-right: -10px;
        margin-left: -10px; }
        html.docs .docs-content .docs-page-content-block .field-wrapper {
          margin-top: 0; }
      html.docs .docs-content .docs-page-content figure.highlight,
      html.docs .docs-content .docs-page-content figure.highlight * {
        display: none; }
    html.docs .docs-content .docs-page-code {
      background: #fafcfc;
      visibility: hidden;
      min-width: 500px;
      flex-basis: 50%;
      flex-grow: 1;
      padding: 10px;
      padding-top: 50px; }
      html.docs .docs-content .docs-page-code .docs-page-content-block > *:not(figure) {
        display: none; }
      html.docs .docs-content .docs-page-code figure {
        margin: 0 0 10px; }
    html.docs .docs-content .docs-page-loader {
      position: absolute;
      background: #ffffff;
      bottom: 0;
      right: 0;
      left: 0;
      top: 0; }
      html.docs .docs-content .docs-page-loader img {
        transform: translate(50%, 50%);
        position: absolute;
        bottom: 50%;
        right: 50%; }

html.docs .site-footer {
  display: none; }
