//scss-lint:disable SelectorFormat

@font-face {
    font-family: 'Nucleo Glyph';
    src: url('#{$nc-font-path}/nucleo-glyph.eot');
    src: url('#{$nc-font-path}/nucleo-glyph.eot') format('embedded-opentype'),
    url('#{$nc-font-path}/nucleo-glyph.woff2') format('woff2'),
    url('#{$nc-font-path}/nucleo-glyph.woff') format('woff'),
    url('#{$nc-font-path}/nucleo-glyph.ttf') format('truetype'),
    url('#{$nc-font-path}/nucleo-glyph.svg') format('svg');
    font-weight: normal;
    font-style: normal;
}

.icon,
.#{$nc-css-prefix} {
    display: inline-block;
    font: normal normal normal #{$nc-font-size-base}/1 'Nucleo Glyph';
    font-size: inherit;
    speak: none;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.#{$nc-css-prefix}.lg {
    font-size: 1.33333333em;
    vertical-align: -16%;
}

.#{$nc-css-prefix}.x2 {
    font-size: 2em;
}

.#{$nc-css-prefix}.x3 {
    font-size: 3em;
}

.#{$nc-css-prefix}.square,
.#{$nc-css-prefix}.circle {
    padding: $nc-padding-width;
    vertical-align: -16%;
    background-color: $nc-background-color;
}

.#{$nc-css-prefix}.circle {
    border-radius: 50%;
}


.#{$nc-css-prefix}-ul {
    padding-left: 0;
    margin-left: $nc-li-width;
    list-style-type: none;

    > li {
        position: relative;
    }

    > li > .#{$nc-css-prefix} {
        position: absolute;
        left: -($nc-li-width / 2) - 0.5em;
        top: (2em / 14);
        text-align: center;

        &.lg {
            top: 0;
            left: -($nc-li-width / 2) - (4em / 14);
        }

        &.circle,
        &.square {
            top: (2em / 14) - $nc-padding-width;
            left: -($nc-li-width / 2) - 0.5em - $nc-padding-width;
        }
    }
}

.#{$nc-css-prefix}.spin {
    -webkit-animation: nc-icon-spin 2s infinite linear;
    -moz-animation: nc-icon-spin 2s infinite linear;
    animation: nc-icon-spin 2s infinite linear;
}

@-webkit-keyframes nc-icon-spin {
    0% {
        -webkit-transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
    }
}
@-moz-keyframes nc-icon-spin {
    0% {
        -moz-transform: rotate(0deg);
    }
    100% {
        -moz-transform: rotate(360deg);
    }
}
@keyframes nc-icon-spin {
    0% {
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
        -moz-transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.#{$nc-css-prefix}.rotate-90 {
    @include nc-rotate(90deg, 1);
}

.#{$nc-css-prefix}.rotate-180 {
    @include nc-rotate(180deg, 2);
}

.#{$nc-css-prefix}.rotate-270 {
    @include nc-rotate(270deg, 3);
}

.#{$nc-css-prefix}.flip-y {
    @include nc-flip(-1, 1, 0);
}
.#{$nc-css-prefix}.flip-x {
    @include nc-flip(1, -1, 2);
}

